.slider{
    overflow: hidden;
    position: relative;
}
.slider img{
    width: 100%;
    height: 100%;
}
.control-btn .next{
    position: absolute;
    top: 40%;
    z-index: 10;
    right: 5%;
}
.control-btn .prev{
    position: absolute;
    top: 40%;
    z-index: 10;
    left: 5%;
}
.slide{
    opacity: 0;
    transition-duration: 1s ease;
}

.slide.active{
    opacity: 1;
    transition-duration: 1s;
    transform: scale(1.08);
}

.control-btn button{
    background-color: rgba(255, 255, 255,0.3);
    width: 60px;
    height: 60px;
    line-height: 60px;
    text-align: center;
    border-radius: 50%;
    color: #fff;
    cursor: pointer;
}
.nhi{
    background-color: #31b675;
    width: 45px;
    height: 45px;
    margin: auto;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}


/* ####### From ########## */

.slide-form h2{
    margin-top: 20px;
}
.slide-form{
    position: absolute;
    top: 33%;
    left: 10%;
    background-color: rgba(0, 0,0,0.7);
    width: 35%;
    height: 75vh;
    border-radius: 5px;
    color: #fff;
}

.slide-form input{
    margin-top: 12px;

}

span,h2,form{
    padding: 0rem 1rem 0 1rem;
}

.slide-form .flex_space input:nth-child(odd) {
    margin-right: 20px;
}
.slide-form .submit{
    background-color: #31b675;

}


@media screen and (max-width:768px){
    .slider{
        height: 65vh;
    }
    .slider img{
        height: 65vh;
    }
    .slide-form{
        left: 5%;
        top: 32%;
        width: 80%;
        height: 60vh;
        margin-top: 3rem;
    }
}